import conf from './conf'
import axios from 'axios'
import md5 from 'js-md5'
import time from 'time-stamp'
import ElementUI from 'element-ui';

var timestamp = Date.now()

var app = {
	http: {
		get: function(uri, callback) {
			axios({
				method: 'get',
				url: conf.api.host + uri,
				headers: {
					token: md5(uri + '@' + app.user.utoken + '#timestamp'),
					uid: app.user.uid,
					timestamp: timestamp
				}
			}).then(function(rs) {
				callback(rs.data, false)
			}).catch(function(error) {
				callback({
					code: -500
				}, error)
			})
		},
		post: function(uri, data, callback) {
			if(app.user.utoken == -1) {
				ElementUI.Message({
					message: '现在是体验账号,请注册正式账号',
					type: 'error'
				})
				window.location.hash = '/login'
				return false
			}
			axios({
				method: 'post',
				url: conf.api.host + uri,
				data: data,
				headers: {
					token: md5(uri + '@' + app.user.utoken + '#timestamp'),
					uid: app.user.uid,
					timestamp: timestamp
				}
			}).then(function(rs) {
				callback(rs.data, false)
			}).catch(function(error) {
				callback({
					code: -500
				}, error)
			})
		},
		delete: function(uri, callback) {
			if(app.user.utoken == -1) {
				ElementUI.Message({
					message: '现在是体验账号,请注册正式账号',
					type: 'error'
				})
				window.location.hash = '/login'
				return false
			}
			axios({
				method: 'delete',
				url: conf.api.host + uri,
				headers: {
					token: md5(uri + '@' + app.user.utoken + '#timestamp'),
					uid: app.user.uid,
					timestamp: timestamp
				}
			}).then(function(rs) {
				callback(rs.data, false)
			}).catch(function(error) {
				callback({
					code: -500
				}, error)
			})
		},
		put: function(uri, data, callback) {
			if(app.user.utoken == -1) {
				ElementUI.Message({
					message: '现在是体验账号,请注册正式账号',
					type: 'error'
				})
				return false
			}
			axios({
				method: 'put',
				url: conf.api.host + uri,
				data: data,
				headers: {
					token: md5(uri + '@' + app.user.utoken + '#timestamp'),
					uid: app.user.uid,
					timestamp: timestamp
				}
			}).then(function(rs) {
				callback(rs.data, false)
			}).catch(function(error) {
				callback({
					code: -500
				}, error)
			})
		}
	},
	user: {
		utoken: 1,
		uid: '1',
		username: 'test'
	}
}

export default app


