import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/Index'
import Category from '@/page/note/Category'
import List from '@/page/note/List'
import Show from '@/page/note/Show'
import ShowTable from '@/page/note/ShowTable'
import Add from '@/page/note/Add'
import AddTable from '@/page/note/AddTable'
import Login from '@/page/Login'

Vue.use(Router)

export default new Router({
	routes: [{
			path: '/',
			name: 'Index',
			component: Index,
			children: [{
				path: '',
				component: List
			}, {
				path: 'list',
				component: List
			}, {
				path: 'show',
				component: Show
			}, {
				path: 'show-table',
				component: ShowTable
			}, {
				path: 'add',
				component: Add
			}, {
				path: 'add-table',
				component: AddTable
			}]
		},
		{
			path: '/category',
			name: 'Category',
			component: Category
		},
		{
			path: '/login',
			name: 'Login',
			component: Login
		}
	]
})


