<template>
	<div class="page">
		<el-container>
			<el-header style="text-align: center;">
				<span style="float: left;" @click="goBack"><el-button size="mini" plain type="primary" icon="el-icon-arrow-left">返回</el-button></span>
			</el-header>
			<el-main>
				<el-form ref="form">
					<el-form-item>
						<el-input @change="save" v-model="info.title"></el-input>
					</el-form-item>
					<el-form-item>
						<el-input type="textarea" :rows="rows" v-model="info.content" @change="save" class="textarea"></el-input>
					</el-form-item>
				</el-form>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'Note',
		data() {
			return {
				rows: 10,
				info: {
					title: '',
					content: ''
				}
			}
		},
		components: {},
		methods: {
			goBack: function() {
				this.$router.go(-1)
			},
			save: function(e) {
				var id = this.$route.query.id
				var that = this
				var data = {
					title: this.info.title,
					content: this.info.content
				}
				this.$http.put('/note/' + id, data, function(rs, err) {
					if(rs.code >= 0) { 
						that.$message({
							message: '数据已保存',
							type: 'success'
						})
					}
				})
			},
			getInfo: function(id) {
				var that = this
				this.$http.get('/note/' + id, function(rs, error) {
					if(!error) {
						that.info = rs.data
					}
				})
			}
		},
		beforeRouteLeave(to, from, next) {
			this.save()
			next()
		},
		created() {
			var id = this.$route.query.id
			var h = document.body.scrollHeight
			if(h > 300) {
				var rows = Math.ceil(h / 30)
				if(rows > 30) {
					rows = 30
				}
				this.rows = rows
			}
			this.getInfo(id)
		}
	}
</script>

<style scoped>
	.addbar {
		padding: 10px 0 0 0;
	}
	
	.el-textarea * {
		background: #FFFACD;
	}
	
	textarea {
		height: 100%;
		min-height: 300px;
	}
</style>


