<template>
	<div class="page">
		<el-container>
			<el-header>
				<h5>{{categoryInfo.name}}</h5>
				<el-dropdown style="float: right;">
					<router-link :to="{path:'add',query:{tid:$route.query.tid}}">
						<el-button type="success">
							<i class="el-icon-plus el-icon--right"></i> 添加记事
						</el-button>
					</router-link>

					<el-dropdown-menu slot="dropdown">
						<router-link :to="{path:'add-table',query:{tid:$route.query.tid}}">
							<el-dropdown-item><i class="el-icon-date el-icon--right"></i> 添加表格</el-dropdown-item>
						</router-link>
					</el-dropdown-menu>
				</el-dropdown>

			</el-header>
			<el-main>
				<template  v-for="v in datas.list">
					<router-link v-if="v.type=='table'" :to="{path:'show-table',query:{id:v.id}}">
						<Feed :title="v.title" content="点击查看表格" type="table" :datetime="v.createtime"></Feed>
					</router-link>
					<router-link v-else :to="{path:'show',query:{id:v.id}}">
						<Feed :title="v.title" content="点击查看内容" :datetime="v.createtime"></Feed>
					</router-link>
				</template>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	import Feed from '@/components/Feed'

	export default {
		name: 'List',
		data() {
			return {
				datas: [],
				categoryInfo: {
					name: '全部'
				}
			}
		},
		components: {
			Feed: Feed
		},
		methods: {
			add: function(row) {
				this.$prompt('请输入标题', '添加新记事', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputPattern: /^[\u4e00-\u9fa5\w]{1,10}$/,
					inputErrorMessage: '1~30个字符（汉字、字母、数字）组成'
				}).then(({
					value
				}) => {
					this.$message({
						type: 'success',
						message: '标题是: ' + value
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			del: function(row) {
				this.$confirm('此操作会永久删除内容, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$message({
						type: 'success',
						message: '删除成功!'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			goHome: function() {
				this.$router.push('/')
			},
			list: function() {
				//列表只对首页进行缓存
				
				var that = this
				var tid = this.$route.query.tid
				var page = this.$route.query.page
				var key = 'note_list'
				page = parseInt(page)?page:1
				if(tid){
					key = key + '_tid:'+tid
				}
				//如果是第一页,先读取本地缓存
				var cacheData = localStorage.getItem(key)
				if(cacheData && page == 1){
					cacheData = JSON.parse(cacheData)
					that.datas = cacheData
				}
				
				this.$http.get('/note?tag_id=' + tid, function(rs) {
					if(rs.code >= 0) {
						that.datas = rs.data
						//第一页写入本地缓存
						if(page == 1 && rs.data ){
							localStorage.setItem(key,JSON.stringify(rs.data))
						}
					}
				})
			},
			getCategoryInfo: function() {
				var that = this
				var tid = this.$route.query.tid
				if(tid) {
					this.$http.get('/category/' + tid, function(rs) {
						if(rs.code >= 0) {
							that.categoryInfo = rs.data
						}
					})
				} else {
					that.categoryInfo.name = '全部'
				}
			}
		},
		watch: {
			$route() {
				this.list()
				this.getCategoryInfo()
			}
		},
		created() {
			this.list()
			this.getCategoryInfo()
		}
	}
</script>

<style scoped>
	
	.addbar {
		padding: 10px 0 0 0;
	}
	
	.add-btn {
		float: right;
	}
</style>


