<template>
	<div class="">
		<HeaderFrame></HeaderFrame>
		<el-container>
			<el-header>
				<el-row class="addbar">
					<el-col :span="12">
						<span style="float: left;" @click="goBack"><el-button size="mini" plain type="primary" icon="el-icon-arrow-left">返回</el-button></span>
					</el-col>
					<el-col :span="12" style="text-align: right;">
						<el-button type="success" @click="add">添加分类</el-button>
					</el-col>
				</el-row>
			</el-header>
			<el-main>

				<el-table :data="datas.list">
					<el-table-column prop="name" label="分类名称"></el-table-column>
					<el-table-column label="操作" width="100">
						<template slot-scope="scope">
							<el-button @click="del(scope.row)" type="text" size="small">删除</el-button>
							<el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
						</template>
					</el-table-column>
				</el-table>

			</el-main>
		</el-container>

	</div>
</template>

<script>
	import HeaderFrame from '@/components/HeaderFrame'
	import FooterFrame from '@/components/FooterFrame'
	export default {
		name: 'Index',
		data() {
			return {
				datas: []
			}
		},
		components: {
			'HeaderFrame': HeaderFrame,
			'FooterFrame': FooterFrame
		},
		methods: {
			add: function(row) {
				var that = this
				this.$prompt('请输入分类名称', '添加分类', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputPattern: /^[\u4e00-\u9fa5\w]{1,10}$/,
					inputErrorMessage: '1~10个字符（汉字、字母、数字）组成'
				}).then(({
					value
				}) => {
					this.$http.post('/category', {
						name: value
					}, function(rs) {
						if(rs.code >= 0) {
							that.$message({
								type: 'success',
								message: '添加成功'
							});
							that.list()
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			edit: function(row) {
				var that = this
				this.$prompt('请输入分类名称', '编辑分类', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputValue: row.name,
					inputPattern: /^[\u4e00-\u9fa5\w]{1,10}$/,
					inputErrorMessage: '1~10个字符（汉字、字母、数字）组成'
				}).then(({
					value
				}) => {
					that.$http.put('/category/' + row.id, {
						name: value
					}, function(rs) {
						row.name = value
						that.$message({
							type: 'success',
							message: '修改成功'
						});
					})

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			del: function(row) {
				var that = this
				this.$confirm('此操作会永久删除分类, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					that.$http.delete('/category/' + row.id, function(rs) {
						if(rs.code > 0) {
							that.list()
							that.$message({
								type: 'success',
								message: '删除成功!'
							});
						} else {
							that.$message({
								type: 'error',
								message: rs.msg
							});
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			goHome: function() {
				this.$router.push('/')
			},
			goBack: function() {
				this.$router.go(-1)
			},
			list: function() {
				var that = this
				this.$http.get('/category', function(rs) {
					if(rs.code >= 0) {
						that.datas = rs.data
					}
				})
			}
		},
		created() {
			this.list()
		}
	}
</script>

<style scoped>

	.addbar {
		padding: 10px 0 0 0;
	}
</style>


