<template>
	<div class="page">
		<el-container>
			<el-header style="text-align: center;">
				<span style="float: left;" @click="goBack"><el-button size="mini" plain type="primary" icon="el-icon-arrow-left">返回</el-button></span>
			</el-header>
			<el-main>
				<el-form ref="form">
					<el-form-item>
						<el-input @blur="save" v-model="info.title"></el-input>
					</el-form-item>
					<el-form-item>
						<el-table :data="info.table" stripe border style="width: 100%">
							<template v-for="(item,k) in info.head">
								<el-table-column :prop="'key'+k" :label="item.name">
									<template scope="scope">
										<el-input v-if="scope.row.edit>=0" v-model="scope.row['key'+k]"></el-input>
										<span v-else>{{scope.row['key'+k]}}</span>
									</template>
								</el-table-column>
							</template>
							<el-table-column label="操作" width="100">
								<template slot-scope="scope">
									<el-button v-if="scope.row.edit>=0" @click="saveRow(scope.$index)" type="success" size="mini" title="">确定</el-button>
									<template v-else>
										<el-button @click="delRow(scope.$index)" type="text" size="small" title="删除此条">删除</el-button>
										<el-button @click="editRow(scope.$index)" type="text" size="small" title="">编辑</el-button>
									</template>
								</template>
							</el-table-column>
						</el-table>
						<span @click="addRow" style="cursor: pointer;color: limegreen;"><i class="el-icon-circle-plus-outline" style="color: limegreen;"></i> 添加一条数据</span>
					</el-form-item>
				</el-form>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'Note',
		data() {
			return {
				rows: 10,
				info: {
					title: '',
					content: '',
					head: [],
					table: []
				}
			}
		},
		components: {},
		methods: {
			goBack: function() {
				this.$router.go(-1)
			},
			save: function() {
				var id = this.$route.query.id
				var key = 'note_'+id
				var that = this
				var data = {
					title: this.info.title,
					table: this.info.table
				}
				this.$http.put('/note/' + id, data, function(rs, err) {
					if(rs.code >= 0) {
						that.$message({
							message: '数据已保存',
							type: 'success'
						})
					}
					
					//刷新本地缓存
					localStorage.setItem(key,JSON.stringify(that.info))
				})
			},
			getInfo: function(id) {
				var that = this
				var key = 'note_'+id
				
				//先读取本地缓存
				var cacheData = localStorage.getItem(key)
				if(cacheData){
					cacheData = JSON.parse(cacheData)
					that.info = cacheData
				}
				this.$http.get('/note/' + id, function(rs, error) {
					if(!error) {
						if(!rs.data.table[0]) {
							rs.data.table = []
						}
						//写入本地缓存
						localStorage.setItem(key,JSON.stringify(rs.data))
						that.info = rs.data
					}
				})
			},
			delRow: function(index) {
				var data = this.info.table
				var that = this
				this.$confirm('确定删除这条数据吗', '提示', {
					confirmButtonText: '确定删除',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					var newData = []
					for(var k in data) {
						if(index != k) {
							newData.push(data[k])
						}
					}
					that.info.table = newData
					this.save()
				})
			},
			editRow: function(index) {
				var data = this.info.table
				this.info.table = []
				for(var k in data) {
					var item = data[k]
					delete item['edit']
					if(index == k) {
						item['edit'] = index
					}
					this.info.table.push(item)
				}
			},
			saveRow: function(index) {
				var data = this.info.table
				for(var k in data) {
					if(index == k) {
						var item = this.info.table[index]
						delete item.edit
						this.$set(this.info.table, index, item)
					}
				}  
				this.save()
			},
			addRow: function(row) {
				var data = this.info.table
				for(var k in data) {
					delete data[k]['edit']
				}
				this.info.table = data
				this.info.table.push({
					edit: 1
				})

			}
		},
		beforeRouteLeave(to,from,next){
			this.save()
			next()
		},
		created() {
			var id = this.$route.query.id
			var h = document.body.scrollHeight
			if(h > 300) {
				var rows = Math.ceil(h / 30)
				if(rows > 30) {
					rows = 30
				}
				this.rows = rows
			}
			this.getInfo(id)
		}

	}
</script>

<style scoped>
	.addbar {
		padding: 10px 0 0 0;
	}
	
	.el-textarea * {
		background: #FFFACD;
	}
	
	textarea {
		height: 100%;
		min-height: 300px;
	}
</style>


