<template>
	<div class="HeaderFrame">
		<div class="main">
			<div class="logo">{{msg}}</div>
			<div class="right">
				<el-dropdown style="color: #fff;">
					<span class="el-dropdown-link">游客<i class="el-icon-arrow-down el-icon--right"></i></span>
					<el-dropdown-menu slot="dropdown"> 
						<div style="width: 300px;height: 150px;padding: 10px;">登陆</div>
					</el-dropdown-menu>
				</el-dropdown>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'HeaderFrame',
		data() {
			return {
				msg: 'demo'
			}
		}
	}
</script>

<style scoped>
	.HeaderFrame {
		text-align: left;
		padding: 10px;
		color: #fff;
		background: #1C86EE;
		margin-bottom: 10px;
		overflow: hidden;
	}
	.logo{
		float: left;
	}
	.right {
		color: #fff;
		float: right;
	}
</style>


