<template>
	<div class="Feed">
		<i v-if="type=='table'" class="el-icon-date el-icon--right"></i> {{ title }}
		<p>{{content}}</p>
		<p class="datetime">{{datetime}}</p> 
	</div>
</template>

<script>
	export default {
		name: 'Feed',
		props: ['title', 'datetime', 'content','type']
	}
</script>

<style scoped>
	.Feed {
		margin: 10px 0;
		border-bottom: 1px #eee solid;
		padding: 5px;
	}
	
	p {
		color: #ccc;
	}
	
	p.datetime {
		font-size: 12px;
		text-align: right;
	}
</style>


