<template>
	<div class="page">
		<el-header>
			添加新记事
		</el-header>
		<el-container>
			<el-main>
				<el-form>
					<el-form-item>
						<el-input v-model="info.title"></el-input>
					</el-form-item>
					<el-form-item>
						<el-input type="textarea" :rows="rows" v-model="info.content" class="textarea"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="success" @click="add">添加</el-button>
						<el-button @click="cancel">返回</el-button>
					</el-form-item>
				</el-form>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'Add',
		data() {
			return {
				rows: 10,
				info: {
					title: '',
					content: ''
				}
			}
		},
		components: {},
		methods: {
			cancel: function() {
				this.$confirm('此操作会丢失已编写的数据，确定返回吗?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$router.push('/')
				})
			},
			add: function() {
				var that = this
				if(!this.info.title) {
					this.$alert('标题不能空');
				}
				var tid = this.$route.query.tid
				var data = {
					title: this.info.title,
					content: this.info.content,
					tag_id: tid
				}
				this.$http.post('/note', data, function(rs) {
					if(rs.code >= 0) {
						that.$message({
							type: 'success',
							message: '添加成功'
						});
						that.$router.push('/list?tid=' + tid)
					}
				})
			}
		},
		created() {
			var h = document.body.scrollHeight
			if(h > 300) {
				var rows = Math.ceil(h / 30)
				if(rows > 30) {
					rows = 30
				}
				this.rows = rows
			}
		}
	}
</script>

<style scoped>
	textarea {
		height: 100%;
		min-height: 300px;
	}
</style>


