<template>
	<div class="page">
		<el-header>
			添加新表格
		</el-header>
		<el-container>
			<el-main>
				<el-form>
					<el-form-item>
						<el-input placeholder="输入标题" v-model="info.title"></el-input>
					</el-form-item>
					<el-form-item>
						<h4>表头属性</h4>
						<el-table :data="info.head" stripe border style="width: 100%">
							<el-table-column prop="name" label="表头名称">
							</el-table-column>
							<el-table-column label="操作" width="100">
								<template slot-scope="scope">
									<el-button @click="delRow(scope.$index)" type="text" size="small" title="删除此条">删除</el-button>
								</template>
							</el-table-column>
						</el-table>
						<span @click="addRow" style="cursor: pointer;color: limegreen;"><i class="el-icon-circle-plus-outline" style="color: limegreen;"></i> 添加一列</span>
					</el-form-item>
					<el-form-item>
						<el-button type="success" @click="add">添加表格</el-button>
						<el-button @click="cancel">返回</el-button>
					</el-form-item>
				</el-form>
			</el-main>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'AddTable',
		data() {
			return {
				rows: 10,
				info: {
					title: '',
					content: '',
					head: [{'name':'标题'},{'name':'备注'}]
				}
			}
		},
		components: {},
		methods: {
			cancel: function() {
				this.$confirm('此操作会丢失已编写的数据，确定返回吗?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$router.push('/')
				})
			},
			delRow: function(index) {
				var data = this.info.head
				var newData = []
				for(var k in data) {
					if(index != k) {
						newData.push(data[k])
					}
				}
				this.info.head = newData
			},
			addRow: function(row) {
				var that = this
				this.$prompt('请输入单元格名称', '添加表头', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					inputValue: row.name,
					inputPattern: /^[\u4e00-\u9fa5\w]{1,10}$/,
					inputErrorMessage: '1~10个字符（汉字、字母、数字）组成'
				}).then(({
					value
				}) => {
					that.info.head.push({
						"name": value
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '你已取消'
					});
				});
			},
			add: function() {
				var that = this
				if(that.info.head.length == 0) {
					that.$alert('表头不能空');
					return
				}
				if(!that.info.title) {
					that.$alert('标题不能空');
					return
				}
				this.$confirm('添加后表头将不能再修改', '提示', {
					confirmButtonText: '确定添加',
					cancelButtonText: '编辑表头',
					type: 'warning'
				}).then(() => {

					var tid = that.$route.query.tid
					var data = {
						title: that.info.title,
						content: that.info.content,
						head: that.info.head,
						table: [],
						tag_id: tid,
						type: 'table'
					}
					that.$http.post('/note', data, function(rs) {
						if(rs.code >= 0) {
							that.$message({
								type: 'success',
								message: '添加成功'
							});
							that.$router.push('/list?tid=' + tid)
						}
					})
					
				})
			}
		},
		created() {
			var h = document.body.scrollHeight
			if(h > 300) {
				var rows = Math.ceil(h / 30)
				if(rows > 30) {
					rows = 30
				}
				this.rows = rows
			}
		}
	}
</script>

<style scoped>

	textarea {
		height: 100%;
		min-height: 300px;
	}
</style>


