<template>
	<div class="">
		<HeaderFrame></HeaderFrame>
		<el-container>
			<el-aside class="aside">
				<el-menu default-active="/list" class="" router unique-opened>
					<el-submenu index="1">
						<template slot="title">
							<i class="el-icon-tickets"></i>
							<span slot="title">记事</span>
							<router-link :to="{path:'category'}" title="添加分类"><i class="el-icon-circle-plus-outline" style="color: limegreen;margin-left: 10px;"></i></router-link>
						</template>
						<el-menu-item-group>
							<el-menu-item index="/list"><i class="el-icon-message" style="color: #ccc;"></i><span style="color: #ccc;">全部</span></el-menu-item>
							<el-menu-item v-for="item in datas.list" :index="'/list?tid='+item.id"><i class="el-icon-message"></i>{{item.name}}</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>

			<el-container>
				<router-view></router-view>
				<el-footer>
					<FooterFrame></FooterFrame>
				</el-footer>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import HeaderFrame from '@/components/HeaderFrame'
	import FooterFrame from '@/components/FooterFrame'
	import Feed from '@/components/Feed'

	export default {
		name: 'Index',
		data() {
			return {
				datas: []
			}
		},
		components: {
			'HeaderFrame': HeaderFrame,
			'FooterFrame': FooterFrame,
			'Feed': Feed
		},
		methods: {
			addCate: function() {
				this.$router.push('note/category')
			},
			changeCate1: function(tid) {
				this.$router.push({
					query: {
						tid: tid
					}
				})
			},
			list: function() {
				var that = this
				this.$http.get('/category', function(rs) {
					if(rs.code >= 0) {
						that.datas = rs.data
					}
				})
			}
		},
		created() {
			this.list()
		}
	}
</script>



